.section2 {
	&::before {
		content: '';
		height: 60px;
		border: 1px solid #EEEEEE;
		background-color: #F9FAFA;
		display: block;
	}

	img {
		max-width: 100%;
	}
}

.flex-1 {
	flex: 1;
}

.flex-2 {
	flex: 2;
}

.coco-title {
	background: gray("200"); /* For browsers that do not support gradients */
	@include border-radius(5px);
	color: $white;
}

.gradient-1 {
  background: linear-gradient(133.9deg, #3DBDB6 0%, #0089CE 100%);
}

.gradient-2 {
  background: linear-gradient(133.9deg, #7A86CC 0%, #9FACF8 100%);
}

.gradient-3 {
  background: linear-gradient(133.9deg, #F3778F 0%, #F26A95 40.86%, #F15A9D 100%);
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
	.section2 {
		.d-flex {
			flex-wrap: wrap;
		}

		.whereToPlay {
			width: 100%;
	    height: auto;
	    padding: 0 20% 5%;
		} 
	}
}
