/* ================================================
  ** IMPORT VENDOR
  include: all 3rd party - font, framework..
  ================================================ */
  @import 'modules/custom-variables';
  @import 'vendor/custom-bootstrap.scss';
  @import 'vendor/owl-carousel/owl.carousel';
  @import 'vendor/owl-carousel/owl.theme.green';
/*-----------*/

/* ================================================
  ** MIXIN, FUNCTIONS
  vendor prefixes support all browsers, it can reuse
  throughout the site.
  ================================================ */
  @import 'partials/mixin';
/*-----------*/

/* ================================================
  ** VARIABLES
  color: text, button, etc.
  size: font size, line height, button, input, etc.
  ================================================ */

  @import 'partials/variables';

/*-----------*/


/* ================================================
  ** IMPORT COMPONENTS
  all parts of site such as header, navbar,
  menu, footer, etc.
  ================================================ */
  
  @import 'partials/base';
  @import 'partials/button';
  @import 'partials/modal';
  @import 'partials/section1';
  @import 'partials/section2';
  @import 'partials/section3';
  @import 'partials/section4';
  @import 'partials/section5';
  @import 'partials/footer';

/*-----------*/
