.section1 {
  background-color: $background-lavender;
  height: 80vh;
  color: $white;
  min-height: 500px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    bottom: 0px;
    right: 0;
    background-image: url("../images/family.png");
    background-repeat: no-repeat;
    width: 712px;
    height: 511px;
    background-size: 100%;
  }

  .center-things {
    width: 570px;
    max-width: 100%; 
  }
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px){
  .section1 {
    height: 60vh;
    
    &::before {
      width: 552px;
      height: 341px;
    }

    .center-things {
      width: 50%;
    }
  }
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .section1 {
    height: 100vh;

    h1 {
      font-size: 1.75rem; 
    }

    &::before {
      width: 332px;
      height: 230px;
      bottom: auto;
      top: calc(50% - 169px - 40px);
      right: 50%;
      @include translateX(50%);
    }

    .center-things {
      width: auto;
      top: auto;
      bottom: 40px;
      @include translateY(0);
    }
  }
}