.big-title {
	-webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  font-size: 2rem;

  &::after {
  	content: "";
  	position: absolute;
  	left: 2px;
  	bottom: -8px;
  	height: 4px;
  	width: 50px;
  	background: gray("200");
  }

  &.big-title-1 {
  	&::after {
  		background: linear-gradient(133.9deg, #3DBDB6 0%, #0089CE 100%);
  	}
  }

  &.big-title-2 {
  	&::after {
		  background: linear-gradient(133.9deg, #F3778F 0%, #F26A95 40.86%, #F15A9D 100%);
  	}
  }
}

.section3 {
	background-color: #FAFAFA;
}

.owl-content {
  .item {
    display: none;

    &.active {
      display: block;
    }
  }
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.animated.infinite {
  animation-iteration-count: infinite;
}

@include keyframes(fadeInUp) {
  from {
    opacity: 0;
    transform: translate3d(0, 15px, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.fadeInUp {
  animation-name: fadeInUp;
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px){
  .owl-theme {
    .owl-nav {
      [class*='owl-'] {
        display: none;
      }
    }
  }
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .owl-theme {
    .owl-nav {
      [class*='owl-'] {
        display: none;
      }
    }
  }
}
