body {
	-webkit-font-smoothing: antialiased;
	-osx-moz-font-smoothing: grayscale;
}

html, body {
	overflow-x: hidden;
	font-family: 'Cabin', sans-serif;
}

.this-center {
	margin: 0 auto;
}

.center-things {
	position: absolute;
	top: 50%;
	@include translateY(-50%);

	&.p-relative {
		position: relative;
	}
}

@media screen and (min-width: 768px) {
	.container {
		width: 1170px;
		max-width: 100%;
	}
}

@media screen and (min-width: 992px) {
	.container {
		width: 1170px;
		max-width: 100%
	}
}